import React from "react";
import lorem from "./lorem";
import Para from "./Paragraphes";

function App() {
  const [text, setText] = React.useState([]);
  const [input, setInput] = React.useState(0);
  const [canCopy, setCanCopy] = React.useState(false);
  const [copied, setCopied] = React.useState(false);
  const handleSubmit = (e) => {
    e.preventDefault();
    const n = Number(input);
    n < 0
      ? setText(lorem.slice(0, 1))
      : n >= lorem.length
      ? setText(lorem.slice(0, lorem.length))
      : setText(lorem.slice(0, n));
    n !== 0 ? setCanCopy(true) : setCanCopy(false);
  };
  React.useEffect(() => {
    const d = setTimeout(() => {
      setCopied(false);
    }, 2000);
    return () => {
      clearTimeout(d);
    };
  }, [copied]);
  return (
    <main>
      <section>
        <div className="section-center">
          <h3>How much paragraphes you need?</h3>
          <form onSubmit={handleSubmit}>
            <label htmlFor="npara">Paragraphe : </label>
            <input
              onChange={(e) => setInput(e.target.value)}
              type="number"
              id="npara"
              value={input}
            />
            <button type="submit" className="btn">
              Generate
            </button>
          </form>
          <article className="lorem-text">
            <Para data={text} />
          </article>
          {canCopy && (
            <button
              className={`btn copy-btn ${copied && "success"}`}
              onClick={() => {
                navigator.clipboard.writeText(text.join("\n"));
                setCopied(true);
              }}
            >
              copy
            </button>
          )}
        </div>
      </section>
    </main>
  );
}

export default App;
